import { Component, h, Host, Prop } from '@stencil/core';
import { generateClasses } from '../../../global/typescript/functions';
import { markdown } from './utilities/functions';

@Component({
  tag: 'mt-paragrafo',
  styleUrl: 'paragrafo.scss',
  scoped: true,
})
export class Paragrafo {
  @Prop({ reflect: true }) produto: string;

  @Prop({ reflect: true }) apostila: string;

  @Prop({ reflect: true }) ano: string;

  @Prop({ reflect: true }) especialidade?: string;

  @Prop({ reflect: true }) parte?: string;

  render() {
    const { produto, especialidade, apostila, parte, ano } = this;

    return (
      <Host
        class={generateClasses(produto, apostila, ano, especialidade, parte, {
          'mt-paragrafo': true,
        })}
      >
        {markdown(produto, especialidade, apostila, parte, ano)}
      </Host>
    );
  }
}
